import React from 'react';
import { useParams } from 'react-router-dom';

const LessonDetail = () => {
  const { id } = useParams();

  return (
    <div>
      <h1>Lesson {id} Details</h1>
      <p>This section contains lesson-specific information and challenges.</p>
    </div>
  );
};

export default LessonDetail;
