import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import useVoiceRecognition from './useVoiceRecognition';
import LogoAnimation from './LogoAnimation';
import Login from './Login';
import HomeScreen from './HomeScreen';
import LessonGrid from './LessonGrid';
import LessonDetail from './LessonDetail';
import Quiz from './Quiz'; // Import the Quiz component

const App = () => {
  const [feedback, setFeedback] = useState('Say a command to begin!');
  const [progress, setProgress] = useState(0);
  const [points, setPoints] = useState(0);
  const [badges, setBadges] = useState([
    { title: 'First Lesson Completed', earned: false },
    { title: 'Halfway Through', earned: false },
    { title: 'All Lessons Completed', earned: false },
  ]);

  const players = [
    { name: 'Alice', points: 50 },
    { name: 'Bob', points: 30 },
    { name: 'You', points },
  ];

  const { isListening, transcript, error } = useVoiceRecognition((command) => {
    // Command handling logic
    console.log(`Received command: ${command}`);
  });

  return (
    <Router>
      <Routes>
        {/* Route for logo animation */}
        <Route path="/" element={<LogoAnimation />} />

        {/* Route for login page */}
        <Route path="/login" element={<Login />} />

        {/* Route for home screen */}
        <Route
          path="/home"
          element={
            <HomeScreen
              feedback={feedback}
              isListening={isListening}
              transcript={transcript}
              error={error}
              badges={badges}
              players={players}
              progress={progress}
              points={points}
              setFeedback={setFeedback}
            />
          }
        />

        {/* Route for quiz */}
        <Route path="/quiz" element={<Quiz />} />

        {/* Route for lesson grid */}
        <Route path="/lessons" element={<LessonGrid />} />

        {/* Route for individual lesson detail */}
        <Route path="/lesson/:id" element={<LessonDetail />} />
      </Routes>
    </Router>
  );
};

export default App;
