import React from 'react';
import LessonCard from './LessonCard';

const LessonGrid = () => {
  const lessons = [
    { id: 1, title: 'Intro to Python', description: 'Learn Python basics' },
    { id: 2, title: 'Intro to React', description: 'Learn React fundamentals' },
  ];

  return (
    <div className="lesson-grid">
      {lessons.map((lesson) => (
        <LessonCard key={lesson.id} title={lesson.title} description={lesson.description} path={`/lesson/${lesson.id}`} />
      ))}
    </div>
  );
};

export default LessonGrid;
