import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Navbar from './Navbar';
import ProgressGraph from './ProgressGraph'; // Import the ProgressGraph component
import './HomeScreen.css';

const HomeScreen = () => {
  const navigate = useNavigate();
  const [points, setPoints] = useState(100); // Example points value
  const [progressData, setProgressData] = useState([20, 40, 60, 80, 100]); // Example progress data
  const [remarks, setRemarks] = useState("Great Job! Keep it up!");

  const handleQuizStart = () => {
    navigate('/quiz');
  };

  return (
    <div>
      <Navbar />
      <div className="container text-center mt-4">
        <h3>Your Points: {points}</h3>
        
        {/* Render the ProgressGraph here */}
        <ProgressGraph progressData={progressData} remarks={remarks} />

        <button className="btn btn-primary mt-4" onClick={handleQuizStart}>
          Find What's Right for You
        </button>
        <div className="row mt-5">
          <div className="col-md-4">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title">Lessons</h5>
                <p className="card-text">Explore coding lessons tailored for you.</p>
                <button className="btn btn-primary" onClick={() => navigate('/lessons')}>
                  Explore Lessons
                </button>
              </div>
            </div>
          </div>
          <div className="col-md-4">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title">Progress</h5>
                <p className="card-text">Track your coding journey.</p>
                <button className="btn btn-primary" onClick={() => navigate('/progress')}>
                  View Progress
                </button>
              </div>
            </div>
          </div>
          <div className="col-md-4">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title">Leaderboard</h5>
                <p className="card-text">See where you stand among peers.</p>
                <button className="btn btn-primary" onClick={() => navigate('/leaderboard')}>
                  View Leaderboard
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HomeScreen;
